"use client"
import { useEffect, useRef, useState } from 'react'
import { python } from '@codemirror/lang-python'
import { EditorView, basicSetup } from 'codemirror'
import { EditorState } from '@codemirror/state'
import { oneDark } from '@codemirror/theme-one-dark'

export default function SplitEditor() {
  const [isSplit, setIsSplit] = useState(false)
  const [sharedContent, setSharedContent] = useState('# Shared code\nprint("Hello")')
  const [leftContent, setLeftContent] = useState(sharedContent)
  const [rightContent, setRightContent] = useState(sharedContent)

  // Sync content when in split view
  useEffect(() => {
    if (isSplit) {
      setLeftContent(sharedContent)
      setRightContent(sharedContent)
    }
  }, [isSplit, sharedContent])

  const Editor = ({ content, onContentChange }) => {
    const editorRef = useRef(null)

    useEffect(() => {
      const state = EditorState.create({
        doc: content,
        extensions: [
          basicSetup,
          python(),
          oneDark,
          EditorView.lineWrapping,
          EditorView.updateListener.of(update => {
            if (update.docChanged) {
              onContentChange(update.state.doc.toString())
            }
          }),
          EditorView.theme({
            "&": { height: "500px" },
            ".cm-scroller": { overflow: "auto" }
          })
        ]
      })

      const view = new EditorView({
        state,
        parent: editorRef.current
      })

      return () => view.destroy()
    }, [content, onContentChange])

    return <div ref={editorRef} className="flex-1" />
  }

  return (
    <div className="p-4">
      <button 
        onClick={() => setIsSplit(!isSplit)}
        className="mb-4 px-4 py-2 bg-blue-500 text-white rounded"
      >
        {isSplit ? "Single View" : "Split View"}
      </button>

      <div className="flex gap-4">
        <div className="flex-1 border rounded">
          <Editor
            content={isSplit ? sharedContent : leftContent}
            onContentChange={isSplit ? setSharedContent : setLeftContent}
          />
        </div>
        
        {isSplit && (
          <div className="flex-1 border rounded">
            <Editor
              content={sharedContent}
              onContentChange={setSharedContent}
            />
          </div>
        )}
      </div>
    </div>
  )
}